import React from 'react';
import { useNavigate } from 'react-router-dom';


const MainPage = () => {
  const user = JSON.parse(sessionStorage.getItem("loginUser"));
  const loginEmail = localStorage.getItem("loginEmail");
  const grade = localStorage.getItem("loginGrade");
  const navigate = useNavigate();

  const handleLogout = () => {
    sessionStorage.removeItem("loginUser");
    localStorage.removeItem("loginEmail");
    localStorage.removeItem("loginGrade");
    window.location.reload();
  };

  return (
    
    <div style={{ textAlign: 'center', marginTop: '50px' }}>
      <h1> My Calendar</h1>
      {user ? (
        <div>
          <p><strong>{user.nickName}</strong>, !</p>
          <button onClick={() => window.location.href = "/calendar"}> </button>
          <button onClick={handleLogout}></button>
          <br /><br />

          {/*    */}
          {grade === "admin" && (
            <button onClick={() => navigate('/list')}>  </button>
          )}

          {/*     */}
          {grade === "user" && loginEmail && (
            <button onClick={() => navigate(`/view?email=${loginEmail}`)}>
                
            </button>
          )}
        </div>
      ) : (
        <div>
          <p>   !</p>
          <button onClick={() => window.location.href = "/login"}></button>
          <button onClick={() => window.location.href = "/join"}></button>
        </div>
      )}
    </div>
  );
};

export default MainPage;
