import React, { useEffect, useState } from 'react';
import FullCalendar from '@fullcalendar/react';
import dayGridPlugin from '@fullcalendar/daygrid';
import CalendarView from './CalendarView';
import '../App.css';
import CalendarWrite from './CalendarWrite';
import interactionPlugin from '@fullcalendar/interaction'; //  
import axios from 'axios';

const Calendar = () => {
  const [events, setEvents] = useState([]);
  const [selectedEvent, setSelectedEvent] = useState(null);
  const [showModal, setShowModal] = useState(false);
  const [selectedDate, setSelectedDate] = useState('');
  const [showWrite, setShowWrite] = useState(false);

  //  
  useEffect(() => {
    fetch("http://localhost:8080/api/calendar/calendarList.do", {
      method: "GET",
      credentials: "include"
    })
      .then(res => res.ok ? res.json() : Promise.reject("  "))
      .then(data => {
        const mapped = data.map(vo => ({
          id: vo.id,
          title: vo.title,
          start: vo.start_date,
          end: vo.end_date,
          color: vo.color
        }));
        setEvents(mapped);
      })
      .catch(err => alert(err));
  }, []);

  //     
  const handleEventClick = (info) => {
    const id = info.event.id;
    fetch(`http://localhost:8080/api/calendar/calendarView.do?id=${id}`, {
      credentials: 'include'
    })
      .then(res => res.ok ? res.json() : Promise.reject("  "))
      .then(vo => {
        setSelectedEvent(vo);
        setShowModal(true);
      })
      .catch(err => alert(err));
  };

  //        
  const handleDateClick = (info) => {
    setSelectedDate(info.dateStr); //   
    setShowWrite(true); //  
  };

  const handleAddEvent = async (newEvent) => {
    try {
      const response = await axios.post('http://localhost:8080/api/calendar/calendarWrite.do', newEvent,{
        withCredentials: true //    
      }); //  URL
      
      const savedEvent = response.data; //    (id )
      //  FullCalendar     
      const calendarEvent = {
        id: savedEvent.id,
        title: savedEvent.title,
        start: savedEvent.start_date,
        end: savedEvent.end_date,
        allDay: savedEvent.all_day === 'Y', //  'Y'  boolean true
        color: savedEvent.color
      };
  
      setEvents([...events, calendarEvent]); // DB  id   
      setShowWrite(false); //  
    } catch (error) {
      console.error("  ", error);
    }
  };
  

  return (
    <>
      <div className="calendar-wrapper">
        <FullCalendar
          plugins={[dayGridPlugin, interactionPlugin]}
          initialView="dayGridMonth"
          events={events}
          eventClick={handleEventClick}
          dateClick={handleDateClick}
        />
        
      </div>

      {/*    */}
      {showModal && selectedEvent && (
        <CalendarView event={selectedEvent} onClose={() => setShowModal(false)} />
      )}

      {/*     - start, end , onClose -   , onSave -     */}
      {showWrite && (
        <CalendarWrite
          date={selectedDate}
          onClose={() => setShowWrite(false)}
          onSave={handleAddEvent}
        />
      )}
    </>
  );
};

export default Calendar;
