import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router-dom'; //  navigate 
import './memberView.css';

function MemberView() {
  const [view, setView] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [showPasswordPrompt, setShowPasswordPrompt] = useState(false);
  const [inputPassword, setInputPassword] = useState("");
  const [mode, setMode] = useState(""); // "edit" or "delete"

  const navigate = useNavigate(); //   

  const email = new URLSearchParams(window.location.search).get("email");

  useEffect(() => {
    const fetchData = async () => {
      try {
        setLoading(true);
        const response = await axios.get(`http://localhost:8080/api/member/view.do?email=${email}`);
        setView([response.data]);
      } catch (err) {
        setError(err.message || " ");
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, [email]);

  const handlePasswordCheck = async () => {
    try {
      const res = await axios.post("http://localhost:8080/api/member/checkPassword.do", {
        email,
        password: inputPassword,
      });

      if (res.data === true) {
        window.location.href = `/memberUpdate?email=${email}&password=${inputPassword}`;
      } else {
        alert("   .");
      }
    } catch (err) {
      alert("    .");
      console.error(err);
    }
  };

  const handleDeleteMember = async () => {
    if (!window.confirm(" ? ")) return;

    try {
      const res = await axios.post("http://localhost:8080/api/member/memberDelete.do", {
        email,
        password: inputPassword,
      });

      if (res.data === "success") {
        alert("  .");
        sessionStorage.clear();
        localStorage.clear();
        window.location.href = "/";
      } else {
        alert("  .   .");
      }
    } catch (err) {
      alert("   .");
      console.error(err);
    }
  };

  if (loading) return <p>  ...</p>;
  if (error) return <p> : {error}</p>;

  return (
    <div className="member-container">
      <h2 className="member-title">  </h2>
      {view.map(vo => (
        <div key={vo.email}>
          <div className="member-profile">
            <img src={`http://localhost:8080${vo.image}`} alt="" className="member-image" />
          </div>

          <table className="member-table">
            <tbody>
              <tr><th></th><td>{vo.email}</td></tr>
              <tr><th></th><td>{vo.name}</td></tr>
              <tr><th></th><td>{vo.nickName}</td></tr>
              <tr><th></th><td>{vo.birth?.slice(0, 10)}</td></tr>
              <tr><th></th><td>{vo.gender}</td></tr>
              <tr><th></th><td>{vo.tel}</td></tr>
              <tr><th></th><td>{vo.grade}</td></tr>
              <tr><th></th><td>{vo.city}</td></tr>
              <tr><th></th><td>{vo.regDate?.slice(0, 10)}</td></tr>
              <tr><th> </th><td>{vo.last_login?.slice(0, 10)}</td></tr>
              <tr><th></th><td>{vo.status}</td></tr>
            </tbody>
          </table>

          {/* /  */}
          <div style={{ marginTop: "20px" }}>
            <button
              onClick={() => {
                setMode("edit");
                setShowPasswordPrompt(true);
              }}
            >
               
            </button>

            <button
              onClick={() => {
                setMode("delete");
                setShowPasswordPrompt(true);
              }}
              style={{ marginLeft: "10px", color: "red" }}
            >
               
            </button>
          </div>

          {/*   */}
          {showPasswordPrompt && (
            <div style={{ marginTop: '15px' }}>
              <p> :</p>
              <input
                type="password"
                value={inputPassword}
                onChange={(e) => setInputPassword(e.target.value)}
              />
              {mode === "edit" && (
                <button onClick={handlePasswordCheck} style={{ marginLeft: "10px" }}>
                  
                </button>
              )}
              {mode === "delete" && (
                <button onClick={handleDeleteMember} style={{ marginLeft: '10px', color: 'red' }}>
                    
                </button>
              )}
            </div>
          )}

          {/*    */}
          <div style={{ marginTop: '30px' }}>
            <button
              onClick={() => navigate(-1)}
              style={{
                padding: '8px 20px',
                backgroundColor: '#eee',
                border: '1px solid #ccc',
                borderRadius: '5px',
                cursor: 'pointer'
              }}
            >
               
            </button>
          </div>
        </div>
      ))}
    </div>
  );
}

export default MemberView;
