import React, { useEffect, useState } from 'react';
import axios from 'axios';

const Join  = () => {
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [name, setName] = useState("");
    const [nickName, setNickName] = useState("");
    const [birth, setBirth] = useState("");
    const [gender, setGender] = useState("");
    const [tel, setTel] = useState("");
    const [city, setCity] = useState("");
    const [image, setImage] = useState(null);
    const [loading, setLoading] = useState(true);   //  
    const [error, setError] = useState(null);       //  
    const handleSubmit = async () => {
        const formData = new FormData();
        formData.append("email", email);
        formData.append("password", password);
        formData.append("name", name);
        formData.append("nickName", nickName);
        formData.append("birth", birth);
        formData.append("gender", gender);
        formData.append("tel", tel);
        formData.append("city", city);
        if (image) {
          formData.append("imageFile", image);
        } else {
          //    (  required = false )
          formData.append("imageFile", new Blob(), "empty.png");
        }        
        for (let [key, value] of formData.entries()) {
            console.log(key, value); //   imageFile  ,  File  !
          }          
      
        try {
          const response = await axios.post("http://localhost:8080/api/member/join.do", formData, {
            headers: {
              "Content-Type": "multipart/form-data"
            }
          });
      
          alert(" !");
      
          //     
          window.location.href = "/api/member/memberList.do"; //  React Router 
      
        } catch (err) {
          console.error(err);
          setError("   .");
        }
      };
      
const handleChange = (e) => {
    setCity(e.target.value);
};

return (
    <div>
      <h2></h2>
      <label>:</label>
        <input type="text" value={email} onChange={(e) => setEmail(e.target.value)} /><br/>
    <label>:</label>
        <input type="password" value={password} onChange={(e) => setPassword(e.target.value)} /><br/>
    <label>:</label>
        <input type="text" value={name} onChange={(e) => setName(e.target.value)} /><br/>
    <label>:</label>
        <input type='text' value={nickName} onChange={(e) => setNickName(e.target.value)} /><br/>
    <label>:</label>
        <input type='date' value={birth} onChange={(e) => setBirth(e.target.value)}/><br/>
    <label>:</label>
        <input type='radio' name='gender' value='' checked={gender === ''}
         onChange={(e) => setGender(e.target.value)}/>
        <input type='radio' name='gender' value='' checked={gender === ''}
         onChange={(e) => setGender(e.target.value)}/><br/>
    <label>:</label>
        <input type='text' value={tel} onChange={(e) => setTel(e.target.value)} /><br/>
    <label>:</label>
    <select value={city} onChange={handleChange}>
        <option value="">--   --</option>
        <option value=""></option>
        <option value=""></option>
        <option value=""></option>
        <option value=""></option>
        <option value=""></option>
        <option value=""></option>
        <option value=""></option>
        <option value=""></option>
        <option value=""></option>
        <option value=""></option>
        <option value=""></option>
        {/*      */}
      </select><br/>
    <label>:</label>
    <input
        type="file"
        name="imageFile"  //   name  imageFile  !
        onChange={(e) => setImage(e.target.files[0])}
    />
      <button onClick={handleSubmit}></button>
      {error && <p style={{ color: 'red' }}>{error}</p>}
    </div>
    
  );
};

export default Join;